import { OpenBet, HistoryBet } from '../../models/BetOrder';
import _ from 'lodash'
import moment from 'moment'
import teaserOddLineAdjust from './utils/teaserOddLineAdjust'

import Agent from '../../models/Agent';
import AgentTransaction from '../../models/AgentTransaction';
import saveNewHistoryBet from './utils/saveNewHistoryBet'


const confirmResultTeaser = async (openBet) => {

	let betOrderStatus = 'TBD'
	let resultAmount = 0

	const teamLength = openBet.eventOdds.length

	const eventsHaveWon       = _.some(openBet.eventOdds, { status: 'Won' })
	const eventsHaveLost      = _.some(openBet.eventOdds, { status: 'Lost' })
	const eventsHavePush      = _.some(openBet.eventOdds, { status: 'Push' })
	const eventsHavePostponed = _.some(openBet.eventOdds, { status: 'Postponed' })
	const eventsHaveCanceled  = _.some(openBet.eventOdds, { status: 'Canceled' })
	const eventsHaveReview    = _.some(openBet.eventOdds, { status: 'Review' })
	const eventsHavePending   = _.some(openBet.eventOdds, { status: 'Pending' })
	
	const allEventsWon        = _.every(openBet.eventOdds, { status: 'Won' })
	const allEventsLost       = _.every(openBet.eventOdds, { status: 'Lost' })
	const allEventsPush       = _.every(openBet.eventOdds, { status: 'Push' })
	const allEventsCanceled   = _.every(openBet.eventOdds, { status: 'Postponed' })
	const allEventsPostponed  = _.every(openBet.eventOdds, { status: 'Canceled' })
	const allEventsReview     = _.every(openBet.eventOdds, { status: 'Review' })
	const allEventsPending    = _.every(openBet.eventOdds, { status: 'Pending' })

	const eventWonCounter = openBet.eventOdds.reduce((total, event) => total + ( event.status === 'Won' ), 0 )

	if(openBet.orderType !== 'SuperTeaser'){
		switch(true){
			case allEventsWon:
				betOrderStatus = 'Won'
				resultAmount = openBet.wagerDetail.winAmount
				break;
			case allEventsLost:
				betOrderStatus = 'Lost'
				resultAmount = -openBet.wagerDetail.riskAmount
				break;
			case !eventsHaveWon && !eventsHaveLost && !eventsHavePush && !eventsHavePending && ( eventsHavePostponed || eventsHaveCanceled ):
			case allEventsPostponed:
			case allEventsCanceled:
				betOrderStatus = 'Canceled'
				resultAmount = 0
				break;
			case eventsHaveLost:
				betOrderStatus = 'Lost'
				resultAmount = -openBet.wagerDetail.riskAmount
				openBet.eventOdds.map(event => {
					if(event.status !== 'Lost'){
						event.status = 'Closed'
					}
					return event
				})
				break;
			case teamLength === 2 && eventWonCounter === 1 && !eventsHaveLost && !eventsHaveReview && !eventsHavePending && (eventsHavePush || eventsHavePostponed || eventsHaveCanceled):
			case allEventsPush:
				betOrderStatus = 'Push'
				resultAmount = 0
				break;

			case eventsHaveWon && eventWonCounter > 1 && !eventsHaveLost && !eventsHaveReview && !eventsHavePending:
				let teaserOddLine = Number(teaserOddLineAdjust(openBet.orderType, eventWonCounter))
				let winAmount = 0

				if(teaserOddLine > 0){
					winAmount = Math.round(openBet.wagerDetail.betAmount * teaserOddLine / 100)
				}else{
					winAmount = Math.round(openBet.wagerDetail.betAmount / Math.abs(teaserOddLine) * 100)
				}
				betOrderStatus = 'Won'
				resultAmount = Number(winAmount).toFixed()
				break;
			default:
				console.log(openBet.orderNumber + ' teaser not finished')
				return false;
		}
	}




	if(openBet.orderType === 'SuperTeaser'){
		switch(true){
			case allEventsWon && eventWonCounter === 3:
				betOrderStatus = 'Won'
				resultAmount = openBet.wagerDetail.winAmount
				break;
			case allEventsLost:
				betOrderStatus = 'Lost'
				resultAmount = -openBet.wagerDetail.riskAmount
				break;
			case !eventsHaveWon && !eventsHaveLost && !eventsHavePush && !eventsHavePending && ( eventsHavePostponed || eventsHaveCanceled ):
			case allEventsPostponed:
			case allEventsCanceled:
				betOrderStatus = 'Canceled'
				resultAmount = 0
				break;
			case eventsHavePush:
			case eventsHaveLost:
				betOrderStatus = 'Lost'
				resultAmount = -openBet.wagerDetail.riskAmount
				openBet.eventOdds.map(event => {
					if(event.status === 'Lost' || event.status === 'Push'){
						return
					}else{
						event.status = 'Closed'
					}
					return event
				})
				break;
			default:
				console.log(openBet.orderNumber + ' teaser not finished')
				return false;
		}
	}





	if(betOrderStatus !== 'TBD'){


		await saveNewHistoryBet(openBet, betOrderStatus, resultAmount)

		// const newHistoryBet = new HistoryBet({
		// 	orderNumber: openBet.orderNumber,
		// 	orderType: openBet.orderType,
		// 	owner: openBet.owner,
		// 	wagerDetail: openBet.wagerDetail,
		// 	status: betOrderStatus,
		// 	resultAmount: resultAmount,
		// 	eventOdds: openBet.eventOdds,
		// 	createdAt: openBet.createdAt,
		// 	closedAt: moment()
		// })
	
		// await newHistoryBet.save()
		// console.log('saved history teaser bet' + newHistoryBet.orderNumber)
		// await OpenBet.findOneAndRemove({ _id: openBet._id })
		// console.log('deleted openbet ' + openBet.orderNumber)


		// if(betOrderStatus.status === 'Lost'){
		// 	const agent = await Agent.findOneAndUpdate({ _id: req.user.agent }, { '$inc': { 'currentStatus.credit': resultAmount, 'currentStatus.availableCredit': resultAmount } })
		// 	let newAgentTransaction = new AgentTransaction({
		// 		owner:{
		// 			player: req.user._id,
		// 			superAgent : req.user.superAgent,
		// 			agent : req.user.agent
		// 		},
		// 		orderType: openBet.orderType,
		// 		transactionType: 'out',
		// 		creditAmount: resultAmount,
		// 		resultAmount: agent.currentStatus.credit + resultAmount,
		// 		orderNumber: openBet.orderNumber,
		// 		createdAt: moment()
		// 	})
		// 	await newAgentTransaction.save()
		// }





		return true

	}else{
		return false
	}

}

export default confirmResultTeaser